body {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  height: 40px;
  background-color: cornflowerblue;
  display: flex;
  justify-content: space-between;
  margin-bottom: 70px;
}

.logo {
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

ul {
  display: flex;
  list-style: none;
  align-items: center;
  padding-right: 40px;
}
li {
  display: flex;
  margin-left: 30px;
}

.hero {
  width: 80%;
  padding: 10px;
  height: 300px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  box-sizing: border-box;
}
img {
  width: 240px;
  border-radius: 100%;
  margin-left: 190px;
}
.sidebar1 {
  width: 50%;
  margin-right: 20px;
  border-right: 5px solid black;
}

.sidebar2 {
  width: 50%;
  font-family: "Courier New", Courier, monospace;
  margin-right: 80px;
}


footer {
  width: 100%;
  height: 100px;
  background-color: gray;
  margin-top: 100px;
}

.nama {
  text-align: center;
  padding: 30px;
  color: aliceblue;
  font-family: fantasy;
  font-weight: 200;
}